




















































import { Vue, Component, Prop, Model, Watch } from "vue-property-decorator";
import api from "@/microApps/processManage/api";

@Component({})
export default class GroupManage extends Vue {
  loading: boolean = false;
  tableData: Array<any> = [];
  dialog: boolean = false;
  dialogLoading: boolean = false;
  dialogTitle: string = "";
  form: any = {};
  formRules: any = {
    procdefTypeName: {
      required: true,
      message: "该选项为必填项",
      trigger: "change"
    }
  };

  // 分页信息
  pagination: any = {
    size: 10,
    current: 1,
    query: {}
  };
  total: number = 0;
  // 自定义序号
  indexCustom: number = 0;

  created(): void {
    this.getTableData();
  }

  pageSizeChange(size: number) {
    this.pagination.size = size;
    this.getTableData().then();
  }

  async getTableData() {
    this.loading = true;
    try {
      let {
        data: { data, status, message }
      } = await this.$http.post(api.processManage.getAllProcessTypePaging, this.pagination);
      if (status) {
        console.table(data);
        this.tableData = data.records;
        this.total = data.total;
        this.indexCustom = (data.current - 1) * data.size + 1;
      } else {
        this.$message.error(message);
      }
    } finally {
      this.loading = false;
    }
  }

  handleAdd() {
    this.dialog = true;
    this.dialogTitle = "添加流程分类";
    this.form = {};
    this.$nextTick(() => {
      let form: any = this.$refs.form;
      form.clearValidate();
    });
  }

  handleEdit(item: any) {
    this.dialog = true;
    this.dialogTitle = "修改流程分类";
    this.form = JSON.parse(JSON.stringify(item));
    this.$nextTick(() => {
      let form: any = this.$refs.form;
      form.clearValidate();
    });
  }

  handleSureSave() {
    let form: any = this.$refs.form;
    form.validate((flag: boolean) => {
      if (flag) {
        this.saveProcessType();
      }
    });
  }

  async saveProcessType() {
    this.dialogLoading = true;
    try {
      let {
        data: { data, status, message }
      } = await this.$http.post(api.processManage.addProcessType, this.form);
      if (status) {
        this.$message.success(this.form.id ? "修改成功" : "添加成功");
        this.getTableData();
        this.dialog = false;
      } else {
        this.$message.error(message);
      }
    } finally {
      this.dialogLoading = false;
    }
  }

  handleDelete(item: any) {
    this.$confirm("将要删除流程分类：" + item.procdefTypeName + "，是否继续？", "确认", {
      confirmButtonText: "确定",
      cancelButtonText: "取消"
    }).then(() => {
      this.deleteItem(item);
    });
  }

  async deleteItem(item: any) {
    let {
      data: { data, status, message }
    } = await this.$http.get(api.processManage.deleteProcessType + item.id);
    if (status) {
      this.$message.success("删除成功");
      this.getTableData();
    } else {
      this.$message.error(message);
    }
  }
}
