


































































import { Vue, Component, Prop, Model, Watch, Emit } from "vue-property-decorator";
import api from "@/microApps/processManage/api";

@Component({})
export default class ProcessList extends Vue {
  processName: string = "";
  // 分页信息
  pagination: any = {
    size: 10,
    current: 1
  };
  total: number = 0;
  typeOptions: Array<any> = [];
  typeId: string = "";

  tableData: Array<any> = [];
  loading: boolean = false;
  // 自定义序号
  indexCustom: number = 0;
  mounted() {
    this.getAllProcessType();
    this.getTableData();
  }

  handleNew() {
    this.$router.push("/processNew");
  }

  handleEdit(item: any) {
    console.log(item);
    if (item.processStatus === "RELEASED") {
      this.$confirm("确定要编辑流程？此操作会将流程置为未发布状态，是否继续？", "确认", {
        confirmButtonText: "确定",
        cancelButtonText: "取消"
      }).then(() => {
        this.$http
          .get(api.processManage.updateProcessStatus, {
            params: {
              procdefKey: item.procdefKey,
              status: 0
            }
          })
          .then((res: any) => {
            if (!res.data.status) {
              this.$message.error(res.data.message);
            }
          });
        this.$router.push({
          path: "/processNew",
          query: {
            item: JSON.stringify(item)
          }
        });
      });
    } else {
      this.$router.push({
        path: "/processNew",
        query: {
          item: JSON.stringify(item)
        }
      });
    }
  }

  pageSizeChange(size: number) {
    this.pagination.size = size;
    this.getTableData();
  }

  async getAllProcessType() {
    this.loading = true;
    try {
      let {
        data: { data, status, message }
      } = await this.$http.get(api.processManage.getAllProcessType);
      if (status) {
        this.typeOptions = data;
      } else {
        this.$message.error(message);
      }
    } finally {
      this.loading = false;
    }
  }

  //根据关键字，每页查询数量，当前页获得所有链表
  async getTableData() {
    this.loading = true;
    try {
      let {
        data: { data, status, message }
      } = await this.$http.get(api.processManage.getPageProcess, {
        params: {
          processName: this.processName,
          typeId: this.typeId,
          ...this.pagination
        }
      });
      if (status) {
        this.tableData = data.records;
        this.total = data.total;
        this.indexCustom = (data.current - 1) * data.size + 1;
      } else {
        this.$message.error(message);
      }
    } finally {
      this.loading = false;
    }
  }

  //切换到流程详情页面（添加和编辑）
  @Emit("switch")
  switchToProcessDetails() {}

  //根据ID删除流程
  handleDelete(row: any) {
    this.$confirm("是否删除流程相关数据？", "提示", {
      confirmButtonText: "确定",
      cancelButtonText: "取消",
      type: "warning"
    }).then(async () => {
      let {
        data: { data, status, message }
      } = await this.$http.delete(api.processManage.deleteProcdefInfo, {
        params: {
          id: row.id
        }
      });
      if (status) {
        this.$message.success("删除成功");
        this.getTableData();
      } else {
        this.$message.error(message);
      }
    });
  }
}
